import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import { UserService } from 'src/app/user.service';
import { first } from 'rxjs/operators';
@Component({
  selector: 'app-addclass',
  templateUrl: './addclass.component.html',
  styleUrls: ['./addclass.component.scss']
})
export class AddclassComponent implements OnInit {
  addclassform:FormGroup
  teachernames: any;
  constructor(public _userservice:UserService) { }

  ngOnInit() {
    this.addclassform = new FormGroup({
      class: new FormControl('',[Validators.required]),
      classnumeric: new FormControl('',[Validators.required]),
      teachername: new FormControl('',[Validators.required]),
      note: new FormControl('',[Validators.required]),
    })

    this._userservice.Teachernames().pipe(first()).subscribe((res:any)=>{
       console.log(res);
       this.teachernames = res.data;
       console.log(this.teachernames);
    });



  }


  addClassClick(){
    console.log(this.addclassform.value.class);
    console.log(this.addclassform.value.classnumeric);
    console.log(this.addclassform.value.teachername);
    console.log(this.addclassform.value.note);
  }

}
